import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./scss/style.scss";

import VSelect from "vue-select";

Vue.component("v-select", VSelect);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
