<template>
  <div class="shows-carousel pos-relative" ref="container">
    <div
      class="shows"
      ref="shows"
      :style="{ transform: 'translate(' + drag + 'px)' }"
      @mousedown="(event) => dragStart(event)"
      @mouseup="(event) => dragEnd(event)"
    >
      <div class="show" v-for="index in 10" :key="index">
        <img src="@/assets/show.png" alt="" class="show-photo" />
        <div class="details">
          <span class="subtitle">{{ index }}</span>
          <span class="caption">caption</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ShowsCarousel",
  data() {
    return {
      drag: 0,
      mousePosition: null,
    };
  },
  methods: {
    dragStart(event) {
      this.mousePosition = event.screenX;
    },
    dragEnd(event) {
      const screenX = event.screenX;

      const showsWidth = this.$refs.shows.offsetWidth;
      const containerWitdh = this.$refs.container.offsetWidth;
      const finalDrag = this.drag + screenX - this.mousePosition;

      if (finalDrag > 0) {
        this.drag = 0;
        return;
      }

      if (finalDrag + showsWidth - containerWitdh < 0) {
        this.drag = containerWitdh - showsWidth;
        return;
      }

      this.drag = finalDrag;
    },
  },
};
</script>
<style lang="scss" scoped>
.shows-carousel {
  display: flex;
  width: 100%;
  overflow: hidden;

  .shows {
    display: flex;
    width: fit-content;
    position: relative;
    transition: transform 0.3s ease;
    cursor: grab;

    &:active {
      cursor: grabbing;
    }

    .show {
      margin-right: 25px;
      border-radius: 8px;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      min-width: 350px;
      max-width: 350px;

      &:last-child {
        margin-right: 0;
      }

      img {
        width: 100%;
        user-select: none;
      }

      .details {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 10px;
        background-color: $footer-grey;

        span {
          user-select: none;
        }

        .subtitle {
          font-size: 1.8rem;
        }
        .caption {
          font-size: 1.3rem;
        }
      }
    }
  }
}
@media (max-width: 576px) {
  .shows-carousel {
    overflow: auto;
    padding-bottom: 15px;
    .shows {
      .show {
        min-width: 250px;
        max-width: 250px;
      }
    }
  }
}
</style>
