<template>
  <div class="container-wrapper" :class="{ 'no-scroll': open }">
    <Navbar :open="open" @toggleNavbar="toggleNavbar" />
    <router-view />
    <Footer />
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
export default {
  name: "Content",
  components: {
    Navbar,
    Footer
  },
  data() {
    return {
      open: null
    };
  },
  watch: {
    open() {
      if (this.open) {
        document.body.classList.add("no-scroll");
      } else {
        document.body.classList.remove("no-scroll");
      }
    }
  },
  methods: {
    toggleNavbar(value) {
      this.open = value;
    }
  }
};
</script>
