<template>
  <div class="container-wrapper pos-relative">
    <img
      src="@/assets/ic_arrow_carousel.svg"
      v-if="!(0 === photo) && carousel"
      alt="Left Arrow"
      class="arrow"
      @click="photoMinus()"
    />
    <div class="see-more container">
      <div class="texts">
        <span class="title">{{ title }}</span>
        <router-link class="see-more-link" :to="link">
          <span>VER TODOS</span>
          <img src="@/assets/ic_arrow.svg" alt="Arrow pointing right" />
        </router-link>
      </div>
      <div v-if="categories" class="categories">
        <Category
          v-for="category in data"
          :key="category.text"
          :text="category.text"
        />
      </div>
      <div v-if="artists" class="artists">
        <Artist
          v-for="artist in data"
          :key="artist.artistName"
          :text="artist.artistName"
          :imgUrl="artist.url"
        />
      </div>
      <div v-if="carousel" class="carousel">
        <Carousel :data="data" :photo="photo" />
      </div>
    </div>
    <img
      src="@/assets/ic_arrow_carousel.svg"
      alt="Right Arrow"
      class="right-arrow arrow"
      v-if="!(data.length - 1 === photo) && carousel"
      @click="photoPlus()"
    />
  </div>
</template>
<script>
import Category from "@/components/Category";
import Artist from "@/components/Artist";
import Carousel from "@/components/Home/Carousel";
export default {
  name: "SeeMore",
  components: {
    Category,
    Artist,
    Carousel
  },
  props: {
    categories: {
      type: Boolean,
      required: false,
      default: false
    },
    artists: {
      type: Boolean,
      required: false,
      default: false
    },
    carousel: {
      type: Boolean,
      required: false,
      default: false
    },
    data: {
      type: Array,
      required: true
    },
    link: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      photo: 0
    };
  },
  methods: {
    photoMinus() {
      this.photo -= 1;
    },
    photoPlus() {
      this.photo += 1;
    }
  }
};
</script>
<style lang="scss" scoped>
.see-more {
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;

  .texts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;

    .title {
      font-size: 3rem;
    }

    .see-more-link {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: $white;

      span {
        font-size: 1.4rem;
        margin-right: 15px;
      }

      img {
        width: 10px;
      }
    }
  }

  .categories {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px 20px;
  }

  .artists {
    display: grid;
    grid-template-columns: repeat(8, 170px);
    gap: 20px;
    overflow-x: auto;
    justify-content: space-between;
  }

  .carousel {
    overflow: hidden;
  }
}

.right-arrow {
  transform: translateY(-50%) rotate(180deg);
  top: 50%;
  right: 5%;
  width: 25px;
}

.arrow {
  position: absolute;
  cursor: pointer;
  z-index: 3;
}

.arrow:not(.right-arrow) {
  top: 50%;
  transform: translateY(-50%);
  width: 25px;
  left: 5%;
}
@media (max-width: 1199.98px) {
}

@media (max-width: 991.98px) {
}

@media (max-width: 767.98px) {
  .see-more {
    .categories {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px 20px;
    }
  }
}

@media (max-width: 575.98px) {
  .see-more {
    .categories {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      gap: 20px 20px;
    }

    .carousel {
      .layout {
        flex-direction: column;

        img {
          max-width: 350px;
        }
      }
    }
  }
}

@media (max-width: 400px) {
  .see-more {
    .texts {
      flex-direction: column;
      align-items: unset;

      a {
        align-self: flex-end;
        margin-top: 20px;
      }
    }

    .carousel {
      img {
        max-width: 280px;
      }
    }
  }
}
</style>
