<template>
  <div class="container-wrapper">
    <div class="container web">
      <img
        src="@/assets/logo_seu-artista.svg"
        alt="Seu artista logo"
        class="logo-with-text"
      />
      <img
        src="@/assets/logo_seu-artista_no-text.svg"
        alt="Seu artista logo"
        class="no-text"
      />
      <div class="navs">
        <router-link to="/home" exact>Home</router-link>
        <router-link to="/artists">Artistas</router-link>
        <router-link to="/hdiw">Como Funciona</router-link>
        <router-link to="/forartists">Para Artistas</router-link>
        <router-link to="/login" class="no-opacity enter">Entrar</router-link>
        <transparent-button
          path="/signin"
          class="no-opacity"
          text="CADASTRAR-SE"
          :signUp="true"
        />
      </div>
    </div>
    <div class="container mobile">
      <img
        src="@/assets/logo_seu-artista_no-text.svg"
        alt="Seu artista logo"
        class="no-text"
      />
      <div
        class="hamburger"
        @click="toggleNavbar()"
        :class="{ open: open === true, closed: open === false }"
      >
        <div class="stripe"></div>
        <div class="stripe"></div>
        <div class="stripe"></div>
      </div>
    </div>
    <div
      class="mobile-nav"
      :class="{ open: open === true, closed: open === false }"
    >
      <div class="navs">
        <router-link to="/home" exact @click.native="toggleNavbar()"
          >Home</router-link
        >
        <router-link to="/artists" @click.native="toggleNavbar()"
          >Artistas</router-link
        >
        <router-link to="/hdiw" @click.native="toggleNavbar()"
          >Como Funciona</router-link
        >
        <router-link to="/forartists" @click.native="toggleNavbar()"
          >Para Artistas</router-link
        >
        <div class="no-active">
          <router-link to="/login" class="enter" @click.native="toggleNavbar()"
            >Entrar</router-link
          >
          <transparent-button
            path="/signin"
            class="sign-up-button"
            text="CADASTRAR-SE"
            :signUp="true"
            @click.native="toggleNavbar()"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TransparentButton from "@/components/TransparentButton";

export default {
  name: "Navbar",
  components: {
    TransparentButton,
  },
  props: {
    open: {
      required: true,
    },
  },
  methods: {
    toggleNavbar() {
      this.$emit("toggleNavbar", !this.open);
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  margin: 4rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &.web {
    .no-text {
      display: none;
    }

    .navs {
      display: flex;
      align-items: center;
      a {
        font-size: 1.8rem;
        margin: 0 2.5rem;
        text-decoration: none;
        color: $white;

        &:first-child {
          margin-left: 0;
        }

        &:not(.no-opacity) {
          opacity: 0.7;
        }

        &.router-link-active {
          opacity: 1;
          font-weight: 600;
        }

        &.enter {
          text-decoration: underline;
        }

        &.sign-up {
          margin-right: 0;
        }
      }
    }
  }

  &.mobile {
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    align-items: center;
    display: none;

    .no-text {
      z-index: 4;
      width: 50px;
    }

    .hamburger {
      position: absolute;
      left: 0;
      top: 50%;
      width: 30px;
      height: 20px;
      transform: translateY(-50%);
      z-index: 11;

      &.open {
        .stripe {
          &:first-child {
            animation-name: first-stripe-open;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }

          &:nth-child(2) {
            animation-name: second-stripe-open;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }

          &:last-child {
            animation-name: third-stripe-open;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }
        }
      }
      &.closed {
        .stripe {
          &:first-child {
            animation-name: first-stripe-closed;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }

          &:nth-child(2) {
            animation-name: second-stripe-closed;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }

          &:last-child {
            animation-name: third-stripe-closed;
            animation-duration: 0.4s;
            animation-fill-mode: forwards;
          }
        }
      }
      .stripe {
        position: absolute;
        border: 1px solid $white;
        left: 0;
        width: 100%;

        &:first-child {
          top: 0%;
        }

        &:nth-child(2) {
          top: 50%;
          transform: translateY(-50%);
        }

        &:last-child {
          top: 100%;
          transform: translateY(-100%);
        }
      }
    }
  }
}
.mobile-nav {
  width: 60vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  background-color: $footer-grey;
  z-index: 10;
  display: none;

  &.open {
    transform: translateX(0);
    transition: transform 0.5s;
  }
  &.closed {
    transform: translateX(-100%);
    transition: transform 0.5s;
  }

  .navs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    margin-top: 60px;

    a {
      font-size: 1.7rem;
      padding: 25px 30px;
      color: $white;
      text-decoration: none;

      &.router-link-active {
        background-color: rgba($color: $white, $alpha: 0.15);
      }

      &.sign-up-button {
        max-width: 220px;
        font-size: 1.3rem;
        align-items: center;
        justify-content: center;

        &::before {
          width: 11px;
          height: 11px;
          background-size: 11px;
        }
      }

      &.enter {
        text-decoration: underline;
        padding: 0;
        margin: 20px 0;
      }
    }

    .no-active {
      padding: 25px 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;

      .sign-up-button {
        padding: 10px 25px;
      }
    }
  }
}

@media (max-width: 575.98px) {
  .mobile-nav {
    width: 100vw;
  }
}

@media (max-width: 767.98px) {
  .container.web {
    display: none;
  }
  .container.mobile {
    display: flex;
  }
  .mobile-nav {
    display: block;
  }
}

@media (max-width: 991.98px) {
  .container.web {
    .logo-with-text {
      display: none;
    }
    .no-text {
      display: block;
      width: 55px;
    }
    .navs {
      a.button {
        font-size: 1rem;
      }
    }
  }
}

@media (max-width: 1199.98px) {
  .container.web {
    img {
      width: 140px;
    }

    .navs {
      a {
        margin: 0 1.5rem;
      }
    }
  }
}

@keyframes first-stripe-open {
  0% {
    top: 0%;
    transform: translateY(0);
  }
  50% {
    top: 50%;
    transform: translateY(-50%);
  }
  100% {
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
}
@keyframes second-stripe-open {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
@keyframes third-stripe-open {
  0% {
    top: 100%;
    transform: translateY(-100%);
  }
  50% {
    top: 50%;
    transform: translateY(-50%);
  }
  100% {
    top: 50%;
    transform: translateY(-50%) rotate(-45deg);
  }
}
@keyframes first-stripe-closed {
  0% {
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
  50% {
    top: 50%;
    transform: translateY(-50%);
  }
  100% {
    top: 0%;
    transform: translateY(0);
  }
}
@keyframes second-stripe-closed {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes third-stripe-closed {
  0% {
    top: 50%;
    transform: translateY(-50%) rotate(-45deg);
  }
  50% {
    top: 50%;
    transform: translateY(-50%);
  }
  100% {
    top: 100%;
    transform: translateY(-100%);
  }
}
</style>
