import Vue from "vue";
import VueRouter from "vue-router";
import Content from "@/views/Content";
import Home from "@/views/Home";
import Artist from "@/views/Artist";
import HowDoesItWork from "@/views/HowDoesItWork";
import SignIn from "@/views/SignIn";
import ForArtists from "@/views/ForArtists";
import Artists from "@/components/Artists";
import ArtistDetails from "@/components/ArtistDetails";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Content,
    redirect: "/home",
    children: [
      {
        path: "/home",
        name: "Home",
        component: Home,
      },
      {
        path: "/hdiw",
        name: "HDIW",
        component: HowDoesItWork,
      },
      {
        path: "/forartists",
        name: "ForArtists",
        component: ForArtists,
      },
      {
        path: "/signin",
        name: "SignIn",
        component: SignIn,
      },
      {
        path: "/artists",
        name: "ArtistsListing",
        redirect: "/artists/listing",
        component: Artist,
        children: [
          {
            path: "listing",
            name: "Artists",
            component: Artists,
          },
          {
            path: ":artistId",
            name: "ArtistsDetails",
            component: ArtistDetails,
          },
        ],
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
