</template>
<script>
export default {
  name: "SignIn",
  computed: {
    roles() {
      return [
        {
          id: "user",
          label: "Quero Contratar",
          value: false,
        },
        {
          id: "artist",
          label: "Sou Artista",
          value: true,
        },
      ];
    },
    types() {
      return [
        {
          name: "Pessoa Física",
          value: true,
        },
        {
          name: "Pessoa Jurídica",
          value: false,
        },
      ];
    },
    gender() {
      return [
        {
          name: "Masculino",
          value: "M",
        },
        {
          name: "Feminino",
          value: "F",
        },
      ];
    },
  },
  data() {
    return {
      form: {
        isIndividual: null,
        isArtist: null,
      },
    };
  },
};
</script>
<style lang="scss" scoped>
.multiform {
  display: flex;
  flex-direction: column;
  width: 100%;

  .type {
    margin-bottom: 3rem;
    width: 100%;
  }
  .individual {
    display: flex;
    flex-direction: column;
  }
}
.legalEntity,
.legalIndividual,
.individual,
.address {
  display: flex;
  flex-direction: column;
