  min-width: 150px;
}

.register {
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;

  .button {
    background-color: $primary-yellow;
    border: 1px solid $primary-yellow;
    color: white;
    margin-bottom: 30px;
  }

  .label-radio {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

