        color: white;
      }
    }
  }
  .terms {
    color: white;
    font-size: 1.3rem;
    a {
      color: white;
    }
  }
}

@media (max-width: 576px) {
  .line {
    margin-bottom: 1.5rem;
    .label-input,
    .label-select {
      margin-bottom: 20px;
      width: 100%;
      margin-right: 0;
    }
  }
  .custom-select {
    padding: 0;
    max-width: unset;
  }
  .user-roles {
    .roles {
      &:last-child {
        margin-bottom: 0;
      }
      .role {
        margin-bottom: 15px;
      }
    }
  }
}
</style>
